/* -- FOOTER ----- */
            .Footer {
                width: 100%;
                height: 30px;
                position: absolute;
                bottom: 0;
                left: 0;
                display: flex;
                justify-content: flex-start;
                align-items: center;
            }

                .FooterProject {
                    position: relative;
                }

                .FooterText {
                    color: #141414;
                    text-transform: capitalize;
                    font-size: 14px;
                    font-weight: 200;
                    color: #E6E6E6;
                    white-space: nowrap;
                }

                .FooterText2Wrap {
                    display: flex;
                    align-items: center;
                    height: 25px;
                }

                    #FooterText2 {
                        margin-left: 140px;
                    }

                .Bold {
                    font-weight: 700;
                }

                .HeartIcon {
                    width: 15px;
                    height: 12px;
                    margin: 0 3px;
                    background: url('../Assets/Icons/HeartIcon.svg');
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                }

            @media screen and (max-width: 780px){
                .Footer {
                    height: 60px;
                    flex-direction: column-reverse;
                    align-items: flex-start;
                    justify-content: center;
                }

                .FooterText {
                    font-size: 12px;
                }
                #FooterText2 {
                    margin-left: 0;
                }
                #FooterText1 {
                    margin-left: 0;
                }
            }